@charset "utf-8";
html {
    height: 100%;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    -webkit-tap-highlight-color: transparent;
    -webkit-user-select: none;
    -webkit-focus-ring-color: rgba(0, 0, 0, 0);
}
body {
    margin: 0;
    padding: 0;
    background-color: #f8f8f8;
    min-height: 100%;
    height: 100%;
    text-align: center;
    color: #404040;
    position:relative;
    font: 0.16rem/1.5 'Microsoft YaHei UI','Microsoft Yahei','Microsoft Jhenghei',"Lucida Grande", "Lucida Sans Unicode", "helvetica", "tahoma", "arial", "sans-serif";
}
h1, h2, h3, h4, h5, h6, p, figure, form {
    margin: 0;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
img {
    border: 0;
    vertical-align: middle;
    -ms-interpolation-mode: bicubic;
    -webkit-touch-callout:none;
}
a {
    text-decoration: none;
    color: #000;
    -webkit-touch-callout:none;
}
a:focus {
}
a:hover {
}
/* hn */
h1 {
    font-size: 0.213rem;
    line-height: 0.48rem;
}
h2 {
    font-size: 0.187rem;
    line-height: 0.4rem;
}
h3 {
    line-height: 0.32rem;
}
h3, h4, h5, h6 {
    font-size: 0.16rem;
}
/* ul ol dl */
ul, li, ol {
    margin: 0;
    padding: 0;
    list-style: none outside none;
}
ul.has-style li, ol li {
    margin-left: 0.333rem;
}
ul.has-style li {
    list-style: disc;
}
ol li {
    list-style: decimal;
}
ul.inline-style li {
    float: left;
    display: inline;
}
dl {
    margin-bottom: 0px;
}
dt {
    font-weight: bold;
}
dd {
    margin: 0 0 0 0px;
    padding: 0;
}
svg:not(:root) {
    overflow: hidden;
}
/* clearfix */
.clearfix:before, .clearfix:after {
    content: "";
    display: table;
}
.clearfix:after {
    clear: both;
    overflow: hidden;
}
.clearfix {
    zoom: 1;
}
.appearance-none{
    -webkit-appearance:none;
   -moz-appearance:none;
   appearance: none;
}
/* -------------------------------------------------
 * form
 * -------------------------------------------------
*/
button, input, select, textarea {
    font-size: 100%;
    font-family: tahoma;
    margin: 0;
    outline: 0 none;
    vertical-align: baseline;
    *vertical-align: middle; 
}
textarea {
    overflow: auto;
    vertical-align: top;
    resize: none;
}
button, input {
    line-height: normal;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button;
    cursor: pointer;
    *overflow: visible;
}
button[disabled],
input[disabled],
.disable {
    cursor: default;
}
input[type="checkbox"],
input[type="radio"],
.form-radio,
.form-checkbox {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0;
    *height: 0.173rem;
    *width: 0.173rem;
}
fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px 0.24rem;
    padding: 0.35em 0.625em 0.75em;
}
legend {
    border: 0;
    padding: 0;
    white-space: normal;
    *margin-left: -7px;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}
/* -------------------------------------------------
 * css3 & html5
 * -------------------------------------------------
*/
/* html5 */
article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
    display: block;
}
audio, canvas, video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
}
audio:not([controls]) {
    display: none;
    height: 0;
}
input[type="search"] {
    -webkit-appearance: textfield;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
}
:-moz-placeholder {
    color: #999;
}
::-webkit-input-placeholder {
    color: #999;
}
/* -------------------------------------------------
 * 非常见标签
 * -------------------------------------------------
*/
abbr[title] {
    border-bottom: 1px dotted;
}
b, strong {
    font-weight: bold;
}
blockquote {
    margin: 1em 0.533rem;
}
dfn {
    font-style: italic;
}
mark {
    background: #ff0;
    color: #444;
}
code, kbd, pre, samp {
    font-family: monospace, serif;
    _font-family: 'courier new', monospace;
    font-size: 1em;
}
pre {
    margin: 1em 0;
    white-space: pre;
    white-space: pre-wrap;
    word-wrap: break-word;
}
q {
    quotes: none;
}
q:before,
q:after {
    content: '';
    content: none;
}
small {
    font-size: 80%;
}
sub, sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}
sup {
    top: -0.5em;
}
sub {
    bottom: -0.25em;
}




